import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  FlatList,
  ScrollView,
  CheckBox
} from 'react-native'
import { Input } from 'react-native-elements'
import { LoadingSplash, Brand, Frame, Button, ContentProp, CurveViewer } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { ErrorHandler } from '../../../Store/Firebase/Utils'
import FirebasePen from '../../../Store/Firebase/Pen'
import FirebaseAutopig from '../../../Store/Firebase/Autopig'

import { ConsysRefs, TeweRefs } from '../../../Services/utils/defines'
import { DataStringCheck, sortObject } from '../../../Services/utils/formators'

const Autopigs = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const farmId = useSelector(state => state.firebase.activeData&&state.firebase.activeData.farm?state.firebase.activeData.farm:undefined)
  const autopigDatas = useSelector(state => state.firebase.autopigs)
  const pensDatas = useSelector(state => state.firebase.pens)

  const [EditView, setEditView] = useState(0)
  const [SelectedAutopig, setSelectedAutopig] = useState("")

  const [AutoPigDatas, setAutoPigDatas] = useState([])
  const [PreIds, setPreIds] = useState([])
  const [PostIds, setPostIds] = useState([])
  
  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (farmId) {
      dispatch(FirebasePen.action({FarmId: farmId}))
      dispatch(FirebaseAutopig.action({FarmId: farmId}))
    }
  },[farmId])

  useEffect(() => {
    const Autopigs = []
    const ConfigDatas = {PrePen:[], PostPen:[], Coridors:[]}
    if (autopigDatas) {
      if (autopigDatas["Sys_Config"]) {
        var Configs = autopigDatas["Sys_Config"]
        if (Configs.PrePen) ConfigDatas.PrePen = Configs.PrePen
        if (Configs.PostPen) ConfigDatas.PostPen = Configs.PostPen
        if (Configs.Coridors) ConfigDatas.Coridors = Configs.Coridors
      }
      Object.keys(autopigDatas).filter(e => e !== "Sys_Config" && e !== "update_time").forEach(element => {
        var AutoData = autopigDatas[element]
        Autopigs[element] = AutoData
      });
    }
    Autopigs["Sys_Config"] = ConfigDatas
    setAutoPigDatas(Autopigs)
  },[autopigDatas])
  
  const AutoPigList = []
  Object.keys(AutoPigDatas).filter(e => e !== "newAutoData").forEach(element => {
    if (element === "Sys_Config") AutoPigList.push(element)
    else AutoPigList.push("SysId_" + element)
  })
  if (pensDatas && typeof pensDatas !== 'undefined') {
    return(
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Common.backgroundPrimary]}>
      {firebaseError && firebaseError.message && (
        <Text style={[{color:Colors.error}]}>{firebaseError.message}</Text>
      )}
      <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
        {EditView === 0?(
          <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
            <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter, {marginVertical:20}]}>
              <Button Texts={AutoPigList} Actions={[(ele) => {
                var Id = ele.text.replace("SysId_", '')
                if (AutoPigDatas[Id]) {
                  setSelectedAutopig(Id)
                  setEditView(2)
                }
              }]}/>
            </View>
            <Button Texts={['New AutoPig']} Frame={true} Actions={[() => {
              setSelectedAutopig("")
              setEditView(1)
            }]} />
          </View>
        ):(
          <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
            {EditView === 1&&( // Create
              <Input label={'Autopig Sys Id'}
                containerStyle={[Layout.midWidth, {borderWidth:1, marginTop:10}]}
                style={[Layout.fill, Layout.midWidth, Common.textInput]}
                onChangeText={text => {
                  if (text && !isNaN(text)) {
                    setSelectedAutopig(text)
                  }else {
                    setSelectedAutopig("")
                  }
                }}
                value={
                  SelectedAutopig
                }
                selectTextOnFocus
              />
            )}
            {EditView === 2&&( // Edit
              <Button Texts={[SelectedAutopig==="Sys_Config"?SelectedAutopig:"SysId_"+SelectedAutopig]} Title={true}/>
            )}
            {SelectedAutopig !== "Sys_Config"?(
              <FlatList style={[Layout.fullWidth, {maxWidth:900, minHeight:125, maxHeight:500, marginVertical:20, marginRight:10}]}
                data={pensDatas?Object.keys(sortObject(pensDatas)):null}
                renderItem={({item, index}) => {
                  const PenData = pensDatas[item]
                  var _SelectedAutopig = SelectedAutopig
                  if (EditView === 1) _SelectedAutopig = "newAutoData"
                  return(
                    <AutoPigProp AutopigData={AutoPigDatas} Selected={_SelectedAutopig} PenId={item} PenData={PenData} 
                      onChange={(DataRef, data)=>{
                        setAutoPigDatas(old => {
                          var _AutoDatas = [] //{...old}
                          if (old && old[_SelectedAutopig]) _AutoDatas[_SelectedAutopig] = {...old[_SelectedAutopig]}
                          else _AutoDatas[_SelectedAutopig] = []

                          if (old && old[_SelectedAutopig] && old[_SelectedAutopig][item]) _AutoDatas[_SelectedAutopig][item] = {...old[_SelectedAutopig][item]}
                          else if (!_AutoDatas[_SelectedAutopig][item]) _AutoDatas[_SelectedAutopig][item] = {}

                          switch (DataRef){
                            case ConsysRefs.VALVEID:
                              _AutoDatas[_SelectedAutopig][item].consysId = data
                              break;
                            case ConsysRefs.SCALE:
                              _AutoDatas[_SelectedAutopig][item].scale = data
                              break;
                            case ConsysRefs.ACTIVITY:
                              _AutoDatas[_SelectedAutopig][item].activity = data
                              break;
                            case ConsysRefs.TEMPERATURE:
                              _AutoDatas[_SelectedAutopig][item].temp = data
                              break;
                            case ConsysRefs.WATERCOUNT:
                              _AutoDatas[_SelectedAutopig][item].water = data
                              break;
                            case ConsysRefs.VALVEFEED:
                              _AutoDatas[_SelectedAutopig][item].feed = data
                              break;
                            case TeweRefs.VALVEFEED:
                              _AutoDatas[_SelectedAutopig][item].teweId = data
                              break;
                          }

                          var NewDatas = {...old, ..._AutoDatas}
                          return NewDatas
                        })
                      }}
                    />
                  )}
                }
                //Setting the number of column
                numColumns={1}
                keyExtractor={(item, index) => String(index)}
              />
            ):(
              <View style={[Layout.midWidth, {marginVertical:10}]}>
                {AutoPigDatas&&AutoPigDatas["Sys_Config"] &&(
                  <View style={[Layout.fullWidth]}>
                    <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between', marginBottom:10}]}>
                      <Input
                        label="Pre id's (Separated by comma)"
                        onChangeText={text => {
                          var _text = text.replaceAll(',', '')
                          if (text && text != ""){
                            if (DataStringCheck(_text.trim())) {
                              setAutoPigDatas(old => {
                                var New = {...old}
                                New["Sys_Config"].PrePen = text.replaceAll(' ', '').split(',')
                                return New
                              })
                            }
                          }else {
                            setAutoPigDatas(old => {
                              var New = {...old}
                              New["Sys_Config"].PrePen = []
                              return New
                            })
                          }
                        }}
                        keyboardType={'default'}
                        multiline={true}
                        value={AutoPigDatas["Sys_Config"].PrePen.join(', ')}
                        containerStyle={[{width:'49%'}]}
                        style={[Layout.fill, Common.textInput, {width:100, height:70}]}
                      />
                      <Input
                        label="Post id's (Separated by comma)"
                        onChangeText={text => {
                          var _text = text.replaceAll(',', '')
                          if (text && text != ""){
                            if (DataStringCheck(_text.trim())) {
                              setAutoPigDatas(old => {
                                var New = {...old}
                                New["Sys_Config"].PostPen = text.replaceAll(' ', '').split(',')
                                return New
                              })
                            }
                          }else {
                            setAutoPigDatas(old => {
                              var New = {...old}
                              New["Sys_Config"].PostPen = []
                              return New
                            })
                          }
                        }}
                        keyboardType={'default'}
                        multiline={true}
                        value={AutoPigDatas["Sys_Config"].PostPen.join(', ')}
                        containerStyle={[{width:'49%'}]}
                        style={[Layout.fill, Common.textInput, {width:100, height:70}]}
                      />
                    </View>
                    <View style={[Layout.fullWidth, {alignItems:'center'}]}>
                      <Input
                        label="Coridor Scale Id's (Separated by comma)"
                        onChangeText={text => {
                          var _text = text.replaceAll(',', '').replaceAll(' ','')
                          if (_text && _text !== "" && !isNaN(_text)){
                            setAutoPigDatas(old => {
                              var New = {...old}
                              var Coridors = text.replaceAll(' ', '').replaceAll(',,',',').split(',')
                              var uniqueCoridors = [];
                              Coridors.forEach(Sysid => {
                                if (uniqueCoridors.indexOf(Sysid) === -1) uniqueCoridors.push(Sysid)
                              })
                              New["Sys_Config"].Coridors = uniqueCoridors
                              return New
                            })
                          } else {
                            setAutoPigDatas(old => {
                              var New = {...old}
                              New["Sys_Config"].Coridors = []
                              return New
                            })
                          }
                        }}
                        keyboardType={'default'}
                        multiline={false}
                        value={AutoPigDatas["Sys_Config"].Coridors.join(', ').trim()}
                        containerStyle={[Layout.midWidth]}
                        style={[Layout.fill, Common.textInput, {width:100}]}
                      />
                    </View>
                  </View>
                )}
              </View>
            )}
            <Button Texts={[t('ActionButtons.Save'),t('ActionButtons.Cancel')]} Frame={true} Actions={[ele => {
              if (ele.index === 1) {
                setEditView(0)
              } else { //Save new / edit - batch
                var update = true;
                if (SelectedAutopig && SelectedAutopig !== "" && (SelectedAutopig === "Sys_Config" || !isNaN(SelectedAutopig))) {
                  if (autopigDatas && autopigDatas[SelectedAutopig]) {
                    if (EditView === 1) {
                      dispatch(ErrorHandler.action({message: "Autopig error, SysId allready exists"}))
                      update = false 
                    }
                  } else if (SelectedAutopig !== "Sys_Config") {
                    if (EditView ===2) {
                      dispatch(ErrorHandler.action({message: "Autopig error, SysId does not exists"}))
                      update = false 
                    }
                  }
                } else {
                  dispatch(ErrorHandler.action({message: "Autopig Sysid error."}))
                  update = false 
                }

                var AutopigData = EditView === 1?AutoPigDatas["newAutoData"]:AutoPigDatas[SelectedAutopig]
                if (!AutopigData) {
                  dispatch(ErrorHandler.action({message: "Autopig data error, please reload"}))
                  update = false
                }

                if (update) {
                  var _AutopigData = SelectedAutopig==="Sys_Config"?AutopigData:[]
                  if (SelectedAutopig!=="Sys_Config") {
                    var PreIds = []
                    var PostIds = []
                    if (AutopigData && AutopigData["Sys_Config"]) {
                      if (AutopigData["Sys_Config"].PrePen) PreIds = AutopigData["Sys_Config"].PrePen
                      if (AutopigData["Sys_Config"].PostPen) PostIds = AutopigData["Sys_Config"].PostPen
                    } 
                    
                    Object.keys(AutopigData).forEach(element => {
                      var PenData = AutopigData[element]
                      if (!_AutopigData[element]) _AutopigData[element] = {}
                      if (PenData) {
                        if (PenData.scale || PenData.activity || PenData.temp || PenData.water || PenData.feed || PenData.teweId !== "") {
                          if (PenData.scale) _AutopigData[element].scale = PenData.scale
                          if (PenData.activity) _AutopigData[element].activity = PenData.activity
                          if (PenData.temp) _AutopigData[element].temp = PenData.temp
                          if (PenData.water) _AutopigData[element].water = PenData.water
                          if (PenData.feed) _AutopigData[element].feed = PenData.feed
                          if (PenData.teweId) _AutopigData[element].teweId = PenData.teweId
                        }

                        if (PenData.consysId && PenData.consysId !== "") {
                          var ConsysId = element.trim()
                          if (isNaN(ConsysId)) {
                            PreIds.forEach(element => {
                              if (ConsysId.startsWith(element)) ConsysId = ConsysId.slice(element.length, ConsysId.length)
                            });
                            if (isNaN(ConsysId)){
                              PostIds.forEach(element => {
                                if (ConsysId.startsWith(element)) ConsysId = ConsysId.replace(element,'');
                              });
                            }
                          }
                          if (ConsysId !== PenData.consysId) _AutopigData[element].consysId = PenData.consysId
                        }
                      }
                    });
                  }
                  dispatch(FirebaseAutopig.update({FarmId:farmId, AutopigId: SelectedAutopig, AutopigData:_AutopigData})).then(data => {
                    if (data.meta.requestStatus === "fulfilled") { // secound hand update Pens
                      if (SelectedAutopig === "Sys_Config") setEditView(0)
                      else {
                        Promise.all(Object.keys(pensDatas).filter(e => e!=="update_time").map(async penId => {
                          var autopigs = ""
                          var update = false
                          if (pensDatas && pensDatas[penId] && pensDatas[penId].datas && pensDatas[penId].datas.autopigs) autopigs = pensDatas[penId].datas.autopigs
                          if (AutopigData[penId] && !autopigs.includes(SelectedAutopig)) {
                            autopigs += SelectedAutopig
                            update = true;
                          }
                          if (!AutopigData[penId] && autopigs.includes(SelectedAutopig)){
                            autopigs = autopigs.replace(SelectedAutopig, '')
                            update = true;
                          }
                          if (update) {
                            await dispatch(FirebasePen.update({PenId:penId, Pen:{datas:{autopigs:autopigs}}}))
                          }
                        })).then(() => {
                          dispatch(FirebasePen.action({}))
                          setEditView(0)
                        })
                      }
                    }
                  })
                }
              }
            }]} />
          </View>
        )}
      </ScrollView>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Autopigs

const AutoPigProp = ({AutopigData, Selected, PenId, PenData, onChange}) => {
  const { t } = useTranslation()
  const { Layout, Common } = useTheme()

  var PreIds = []
  var PostIds = []
  if (AutopigData && AutopigData["Sys_Config"]) {
    if (AutopigData["Sys_Config"].PrePen) PreIds = AutopigData["Sys_Config"].PrePen
    if (AutopigData["Sys_Config"].PostPen) PostIds = AutopigData["Sys_Config"].PostPen
  } 
  
  var ConsysId = PenId.trim()
  if (isNaN(ConsysId)) {
    PreIds.forEach(element => {
      if (ConsysId.startsWith(element)) ConsysId = ConsysId.slice(element.length, ConsysId.length)
    });
    if (isNaN(ConsysId)){
      PostIds.forEach(element => {
        if (ConsysId.startsWith(element)) ConsysId = ConsysId.replace(element,'');
      });
    }
  }

  var Autopig_PenData = {consysId:ConsysId, scale:false, activity:false, temp:false, water:false, feed:false, teweId:''}
  if (AutopigData && AutopigData[Selected] && AutopigData[Selected][PenId]) {
    var AutoData = AutopigData[Selected][PenId]
    if (AutoData.consysId) Autopig_PenData.consysId = AutoData.consysId
    if (AutoData.scale) Autopig_PenData.scale = AutoData.scale
    if (AutoData.activity) Autopig_PenData.activity = AutoData.activity
    if (AutoData.temp) Autopig_PenData.temp = AutoData.temp
    if (AutoData.water) Autopig_PenData.water = AutoData.water
    if (AutoData.feed) Autopig_PenData.feed = AutoData.feed
    if (AutoData.teweId) Autopig_PenData.teweId = AutoData.teweId
  }

  var ErrorMessage = ""
  if (isNaN(Autopig_PenData.consysId)) {
    ErrorMessage = "Consys does not recognize " + ConsysId + " as a valid Id."
  }

  return(
    <View style={Layout.fullWidth, {marginRight:2}}>
      <View style={[Common.border, isApp()?{}:Layout.row, Layout.fullWidth, {justifyContent:'space-between'}]}>
        <View style={[Layout.row, {minWidth:245, justifyContent:'space-between'}]}>
          <Input
            label="PenName"
            editable={false}
            onChangeText={text => {
            }}
            keyboardType={'default'}
            value={PenId}
            containerStyle={[{width:'49%', minWidth:100 }]}
            style={[Layout.fill, Common.textInput, {width:100, minWidth:100}]}
          />
          <Input
            label="Consys-Id"
            errorMessage={ErrorMessage}
            onChangeText={text => {
              if (text && (text === "" || !isNaN(text))){
                onChange(ConsysRefs.VALVEID, text)
              }
            }}
            keyboardType={'default'}
            value={Autopig_PenData.consysId}
            selectTextOnFocus
            containerStyle={[{width:'49%', minWidth:100 }]}
            style={[Layout.fill, Common.textInput, {width:100, minWidth:100}]}
          />
        </View>
        <View style={[Layout.row, {minWidth:330, justifyContent:'space-between', alignItems:'center'}]}>
          <View style={[Layout.row, {borderWidth:0, height:50, alignItems:'center', justifyContent:'flex-end', minWidth:63}]}>
            <Text style={[{marginLeft: 5}]}>Scale</Text>
            <CheckBox style={[{marginTop:2, marginHorizontal:5}]}
              value={Autopig_PenData.scale}
              onValueChange={() => {
                onChange(ConsysRefs.SCALE, !Autopig_PenData.scale)
              }}
            />
          </View>
          <View style={[Layout.row, {borderWidth:0, height:50, alignItems:'center', justifyContent:'flex-end', minWidth:77}]}>
            <Text style={[{marginLeft: 5}]}>Activity</Text>
            <CheckBox style={[{marginTop:2, marginHorizontal:5}]}
              value={Autopig_PenData.activity}
              onValueChange={() => {
                onChange(ConsysRefs.ACTIVITY, !Autopig_PenData.activity)
              }}
            />
          </View>
          <View style={[Layout.row, {borderWidth:0, height:50, alignItems:'center', justifyContent:'flex-end'}]}>
            <Text style={[{marginLeft: 5}]}>Temp</Text>
            <CheckBox style={[{marginTop:2, marginHorizontal:5}]}
              value={Autopig_PenData.temp}
              onValueChange={() => {
                onChange(ConsysRefs.TEMPERATURE, !Autopig_PenData.temp)
              }}
            />
          </View>
          <View style={[Layout.row, {borderWidth:0, height:50, alignItems:'center', justifyContent:'flex-end'}]}>
            <Text style={[{marginLeft: 5}]}>Water</Text>
            <CheckBox style={[{marginTop:2, marginHorizontal:5}]}
              value={Autopig_PenData.water}
              onValueChange={() => {
                onChange(ConsysRefs.WATERCOUNT, !Autopig_PenData.water)
              }}
            />
          </View>
          <View style={[Layout.row, {borderWidth:0, height:50, alignItems:'center', justifyContent:'flex-end'}]}>
            <Text style={[{marginLeft: 5}]}>Feed</Text>
            <CheckBox style={[{marginTop:2, marginHorizontal:5}]}
              value={Autopig_PenData.feed}
              onValueChange={() => {
                onChange(ConsysRefs.VALVEFEED, !Autopig_PenData.feed)
              }}
            />
          </View>
        </View>
        <View style={[Layout.row, {minWidth:102, justifyContent:'space-between', marginBottom:5}]}>
          <Input
            label={"Tewe-Feed \n(Valve Id)"}
            onChangeText={text => {
              if (text && (text === "" || !isNaN(text))){
                onChange(TeweRefs.VALVEFEED, text)
              }
            }}
            keyboardType={'default'}
            value={Autopig_PenData.teweId}
            selectTextOnFocus
            containerStyle={[{width:'98%', minWidth:100}]}
            style={[Layout.fill, Common.textInput, {width:100, minWidth:100}]}
          />
        </View>
      </View>
    </View>
  )
}