import handleError from '../utils/handleError'
import { FireBase } from '../index'

async function fetch({FarmId, AutopigId, DataTime}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/AutoPigs";
    if (_rend.getState().firebase.autopigs) {
      var time = await FireBase.database().ref(ref + '/update_time').once('value').then((_timeData) => {
        if (_rend.getState().firebase.autopigs.update_time < _timeData.val()) return false
        else return true
      })
      if (time) {
        var StateAutopigs = _rend.getState().firebase.autopigs
        if (!AutopigId && !StateAutopigs[AutopigId]) {
          return StateAutopigs
        }
      }
    }

    if (AutopigId) ref += "/" + AutopigId;
    var res = await FireBase.database().ref(ref).once('value').then(async (autopig) => {
      var Autopigs = null
      var AutopigVal = autopig.val()
      if (AutopigVal) {
        if (AutopigId) {
          var newAutopig = {[graph.key]: AutopigVal}
          Autopigs = {...Autopigs, ...newAutopig}
        } else {
          Autopigs = {...Autopigs, ...AutopigVal}
        }
      } else {
        AutopigId = null
      }
      return Autopigs
    }).catch(error => {
      console.log("Get farm autopig Error: ", error);
      return handleError({ message: 'Get farm autopig Error' })
    })
    return res
  }
  return handleError({ message: 'Fecth Autopigs Error' })
}

async function update({FarmId, AutopigId, AutopigData}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/AutoPigs";
    if (AutopigId) {
      var ti = await FireBase.database().ref(ref).update({
        update_time: FireBase.database.ServerValue.TIMESTAMP
      })

      ref += "/" + AutopigId + "/"

      var res = await FireBase.database().ref(ref).update({
        ...AutopigData
      }).then(async () => {
        var res2 = await fetch({FarmId: FarmId},_rend).then((_AutopigData)=>{
          return _AutopigData
        })
        return res2
      }).catch(error => {
        console.log("Update AutoPigs Error: ", error);
        return handleError({ message: 'Update AutoPigs Error' })
      })
      return res
    }
  }
  return handleError({ message: 'Update AutoPigs Error' })
}

export {
  fetch, 
  update,
}