import React from 'react'
import PropTypes from 'prop-types'
import { View, Image, Picker } from 'react-native'
import { useTheme } from '../Theme'
import { useTranslation } from 'react-i18next'
import i18n from 'i18next'

const LanguageSelector = ({ }) => {
  const { Layout, Images } = useTheme()

  var language = [['English','en'], ['Espaol','es']]
  var _Image = Images.en
  switch(i18n.language){
    case "es":
      _Image = Images.es
      break;
  }
  return (
    <View style={[Layout.fill, Layout.fullSize, {position:'absolute'}]}>
      <Image style={[{height:30, width:40, top:10, right:55, position:'absolute'}]} source={_Image} resizeMode={'contain'} />
      <Picker
        mode="dropdown"
        selectedValue={i18n.language}
        style={ [{width:50, height: 30, position:'absolute', top:10, right:10 }]}
        onValueChange={(itemValue, itemIndex) => {
          i18n.changeLanguage(itemValue)
        }}
      >
        {language.map((lan, i) => {
          return (<Picker.Item label={lan[0]} value={lan[1]} key={i}/>)
        })}
      </Picker>
    </View>
  )
}

LanguageSelector.propTypes = {
}

LanguageSelector.defaultProps = {
}

export default LanguageSelector
