import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update} from '../../Services/Firebase/Autopigs'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('firebase/autopigs', fetch),
  update: buildAsyncActions('firebase/autopigs', update),
  //create: buildAsyncActions('firebase/graphs', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'autopigs',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}