import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update} from '../../Services/Consys/Temperature'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('consys/temperature', fetch),
  //update: buildAsyncActions('consys/temperature', update),
  //create: buildAsyncActions('consys/temperature', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'temperature',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}