import { buildSlice } from '@thecodingmachine/redux-toolkit-wrapper'
import Scale from './Scale'
import Activity from './Activity'
import Temperature from './Temperature'
import Water from './Water'

// This state is common to all the "consys" module, and can be modified by any "consys" reducers
const sliceInitialState = {
  scale: {},
  activity: {},
  temperature: {},
  water: {},
  feed: {},
  teweFeed: {}
}

export default buildSlice('consys', [Scale, Activity, Temperature, Water], sliceInitialState).reducer
