const HTTPS_START = 'https://';

export const Config = {
  TEST_API_URL: 'https://jsonplaceholder.typicode.com/',
}

export const Consys_Config = {
  CONSYS_CLIENT_ID: 'consysapp',
  CONSYS_CLIENT_SECRET: 'Kjsd832n8DIO32kmsdds3',
  CONSYS_AUTH: '.agrisys.cloud/oauthserver/getaccesstoken/?redirect_uri=urn:ietf:wg:oauth:2.0:oob',
  CONSYS_API: '.agrisys.cloud/consys-api-v1/',

  GetToken_URL: (Site_URL, Refresh_Token) => GetToken_URL(Site_URL, Refresh_Token),
  GetCheckToken_URL: (Site_URL) => GetCheckToken_URL(Site_URL),
  GetConsumtion_URL: (SiteURL) => GetConsumtion_URL(SiteURL),
  GetScaleNow_URL: (SiteURL) => GetScaleNow_URL(SiteURL),
  GetConsumtionData_URL: (Site_URL, Consumptiontype, SystemId, Offset=null, Limit=null, Starttime=null, Stoptime=null, Lastid=null) => GetConsumtionData_URL(Site_URL, Consumptiontype, SystemId, Offset, Limit, Starttime, Stoptime, Lastid),
  GetConsumtionData_Var: (Consumptiontype, SystemId, Offset=null, Limit=null, Starttime=null, Stoptime=null, Lastid=null) => GetConsumtionData_Var(Consumptiontype, SystemId, Offset, Limit, Starttime, Stoptime, Lastid)
}

function GetToken_URL(Site_URL, Refresh_Token) {
  var res = HTTPS_START + Site_URL + Consys_Config.CONSYS_AUTH
  res = res + '&client_id='+Consys_Config.CONSYS_CLIENT_ID+'&client_secret='+Consys_Config.CONSYS_CLIENT_SECRET+'&refresh_token=' + Refresh_Token
  return res;
}

function GetCheckToken_URL(Site_URL) {
  var res = HTTPS_START + Site_URL + Consys_Config.CONSYS_API + 'checkaccesstoken'
  return res;
}

function GetConsumtion_URL(Site_URL) {
  var res = HTTPS_START + Site_URL + Consys_Config.CONSYS_API + 'getconsumptiondata?'
  return res;
}

function GetScaleNow_URL(SiteURL) {
  var res = HTTPS_START + SiteURL + Consys_Config.CONSYS_API + 'scalenow?'
  return res;
}

function GetConsumtionData_URL(Site_URL, Consumptiontype, SystemId, Offset=null, Limit=null, Starttime=null, Stoptime=null, Lastid=null){
  var res = ""
  if (Consumptiontype === "scalenow") {
    res = GetScaleNow_URL(Site_URL)
    res = res + 'systemid=' + SystemId + '&pen=' + Offset
  }
  else {
    res = GetConsumtion_URL(Site_URL)
    res = res + GetConsumtionData_Var(Consumptiontype, SystemId, Offset, Limit, Starttime, Stoptime, Lastid)
  }
  return res;
}

function GetConsumtionData_Var(Consumptiontype, SystemId, Offset=null, Limit=null, Starttime=null, Stoptime=null, Lastid=null){
  var res = 'consumptiontype=' + Consumptiontype + '&systemid=' + SystemId;
  if (Offset) res = res + '&offset=' + Offset;
  if (Limit) res = res + '&limit=' + Limit;
  if (Starttime) res = res + '&starttime=' + parseInt(Starttime);
  if (Stoptime) res = res + '&stoptime=' + parseInt(Stoptime);
  if (Lastid) res = res + '&lastid=' + Lastid;
  return res;
}