import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update} from '../../Services/Consys/Activity'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('consys/activity', fetch),
  //update: buildAsyncActions('consys/activity', update),
  //create: buildAsyncActions('consys/activity', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'activity',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}