import handleError from '../utils/handleError'
import { Consys_Axios } from '../index'
import { Check_Access } from './Utils'
import { Consys_Config } from '../../Config'

import { ConsysRefs, DataAvgType } from '../utils/defines'

const SCALE_ID = ConsysRefs.SCALE //700

async function fetch({AvgType, sysId=1, start=null, stop=0}, _rend) {
  var Scale_Data = []
  var state = _rend.getState().consys
  if (state && state.scale) {
    Scale_Data = {...state.scale}
  }

  var DataCall = []
  if (Scale_Data[sysId]) {
    var Sys_Scale = Scale_Data[sysId]
    if (Sys_Scale.oldest.timestamp*1000 > stop) {
      DataCall.push({start:Sys_Scale.oldest.timestamp, stop:stop/1000})
    }
    if (!start || Sys_Scale.lastId.timestamp*1000 < start) {
      DataCall.push({start:null, stop:Sys_Scale.lastId.timestamp})
    }
  } else {
    Scale_Data[sysId] = {data:[]}
    DataCall.push({start:start, stop:stop})
  }

  if (DataCall.length > 0) {
    var Access = await Check_Access(_rend.getState().firebase)
    if (Access && Access.SiteUrl) {
      var limit = 3000
      var res = await Promise.all(DataCall.map(async FetchData => {
        var offset = 0
        var lastId = null

        var DataDone = true
        do
        {
          DataDone = true
          var baseURL = Consys_Config.GetConsumtionData_URL(Access.SiteUrl, SCALE_ID, sysId, offset, limit, FetchData.start, FetchData.stop, lastId)
          //console.log("fetch", baseURL)

          var consumptiondata = await Consys_Axios(baseURL).get().then(async (response)=> {
            if (!response || !response.data) {
              console.log('Consys Scale get data Error')
              return handleError({ message: 'Consys Scale get data Error' })
            }
            var ResData = response.data
            //console.log(ResData)
            if (!ResData.result === "OK"){
              console.log('Consys Scale data result Error')
              return handleError({ message: 'Consys Scale data result Error' })
            }
            if (ResData.consumptiondata) {
              return ResData.consumptiondata
            }else return null
          })

          //console.log(consumptiondata) // todo: if check if error not res
          if (consumptiondata) {
            var data = Scale_Data[sysId].data.concat(consumptiondata).sort((a, b) => a.timestamp>b.timestamp?1:-1)
            var oldest = data[0]
            var lastId = data[data.length-1]
            Scale_Data[sysId] = {data:data, oldest:oldest, lastId:lastId}
            if (consumptiondata.length === 3000) {
              offset += 3000
              DataDone = false
            }
          }
        } while (!DataDone)
      }));
    } else return handleError({ message: 'Consys Scale Access Error' })
  }

  state = _rend.getState().consys
  if (state && state.scale) {
    Scale_Data = {...state.scale, ...Scale_Data}
  }
  //console.log(Scale_Data)
  return Scale_Data
}

export {
  fetch
}